import React, { Component } from 'react'
import CategoryCard from './CategoryCard'

export default class Categories extends Component {
  render() {
    return (
      <div className="container">
        <div className="row">
          <CategoryCard title="Colegios" properties="col-4 p-4 text-center text-light" color="#a40e4c"/>
          <CategoryCard title="Institucional" properties="col-4 p-4 text-center text-light" color="#2c2c54"/>
          <CategoryCard title="Corporativa" properties="col-4 p-4 text-center text-light" color="#acc3a6"/>
        </div>
      </div>
      
    )
  }
}
